i {
	color: black;
	font-size: 23px !important;
	margin-top: 4px;
	margin-right: 4px;
}

.brand-main {
	font-family: 'Lato', sans-serif;
	font-weight: 700;
	line-height: 1.2em;
	letter-spacing: 0.02em;
}

.nav-wrapper {
	font-family: 'Lato', sans-serif;
	line-height: 1.2em;
	letter-spacing: 0.02em;
}

@media only screen and (max-width: 600px) {
  body {
    background-color: lightblue;
  }
  .navbar-brand, .mx-auto, .order-3 {
  	font-size: 13px !important;
	}
}


